<template>
  <div class="newaddres">
    <div class="newaddresbox">
      <div class="newaddres-top">
        <van-nav-bar
          :title="aid?'编辑地址':'新增地址'"
          left-text="返回"
          left-arrow
          @click-left="onClickLeft"
        />
      </div>
      <van-address-edit
        :area-list="areaList"
        show-postal
        :show-delete="!!aid"
        show-set-default
        :address-info="addressInfo"
        show-search-result
        :area-columns-placeholder="['请选择', '请选择', '请选择']"
        @save="clickSave"
        @delete="delAddress"
      />
    </div>
  </div>
</template>

<script>
import "../assets/less/newaddress.less";
import areaList from "../assets/js/arealist";
import { utils } from "../assets/js/utils";
export default {
  created() {
    this.aid = this.$route.query.aid;
    if (this.aid) {
      this.getaddressInfo();
    }
  },
  data() {
    return {
      areaList,
      // 地址默认初始值
      addressInfo: {
        id: "",
        name: "",
        tel: "",
        province: "",
        city: "",
        county: "",
        addressDetail: "",
        areaCode: "",
        postalCode: "",
        isDefault: false,
      },
      aid: "",
    };
  },
  methods: {
    //返回上一级
    onClickLeft() {
      this.$router.go(-1);
    },
    // 点击保存
    clickSave(content) {
      if (this.aid) {
        this.changeAddress(content);
      } else {
        this.createNew(content);
      }
    },
    // 新增地址
    createNew(content) {
      let newContent = Object.assign({}, content);
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push("/login");
      }
      delete newContent.id;
      delete newContent.county;

      newContent.isDefault = Number(newContent.isDefault);
      newContent.appkey = this.appkey;
      newContent.tokenString = tokenString;

      // 参数序列化
      let data = utils.queryString(newContent);

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "POST",
        url: "/addAddress",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 9000) {
            this.$toast(result.data.msg);
            setTimeout(() => {
              this.$router.go(-1);
            }, 600);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //修改地址
    changeAddress(content) {
      let bool = false;
      // 判断地址是否被修改
      for (let key in content) {
        if (this.addressInfo[key] != content[key]) {
          bool = true;
          break;
        }
      }
      if (bool) {
        let newContent = Object.assign({}, content);
        let tokenString = localStorage.getItem("CSTK");
        if (!tokenString) {
          return this.$router.push("/login");
        }
        delete newContent.id;
        delete newContent.county;

        newContent.aid = this.aid;
        newContent.isDefault = Number(newContent.isDefault);
        newContent.appkey = this.appkey;
        newContent.tokenString = tokenString;

        // 参数序列化
        let data = utils.queryString(newContent);

        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0,
          loadingType: "spinner",
        });
        this.axios({
          method: "POST",
          url: "/editAddress",
          data,
        })
          .then((result) => {
            this.$toast.clear();
            if (result.data.code == 30000) {
              setTimeout(() => {
                this.$router.go(-1);
              }, 1000);
            }
          })
          .catch((err) => {
            this.$toast.clear();
          });
      }
    },
    // 根据aid查询地址数据
    getaddressInfo() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push("/login");
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findAddressByAid",
        params: {
          appkey: this.appkey,
          tokenString,
          aid: this.aid,
        },
      })
        .then((result) => {
          this.$toast.clear();
          //
          for (let key in this.addressInfo) {
            if (key == "id") {
              this.addressInfo[key] = result.data.result[0].aid;
              continue;
            }
            this.addressInfo[key] =
              key == "isDefault"
                ? Boolean(result.data.result[0][key])
                : result.data.result[0][key];
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //删除地址
    delAddress() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push("/login");
      }
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        aid: this.aid,
      });
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "post",
        url: "/deleteAddress",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 10000) {
            this.$toast(result.data.msg);
            setTimeout(() => {
              this.$router.go(-1);
            }, 600);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>